.Mag {
    width: 100%;
    background: #222133;
    border-radius: 15px;
    padding: 17px;

    &-Table {
        width: 100%;
        display: flex;
        flex-direction: column;

        &-Control {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            h1 {
                font-weight: 500;
                font-size: 22px;
                line-height: 26px;
                color: #c4c2e4;
                text-align: left;
            }
        }

        &-Add {
            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-top: 18px;

            p {
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
                color: #ffffff;
                padding: 18px 10px;
                background: linear-gradient(113.21deg, #5d277f 0%, #af3f65 52.5%, #ff7173 124.44%);
            }
        }

        &-Title {
            margin-top: 60px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 16px;
            // text-align: left;

            p {
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
                color: #c4c2e4;
                flex: 1;
                // width: 100%;
                // text-align: center;
            }
        }
    }
}
